import { lazy } from "react";
import TeamMember1 from "../content/TeamMember1.json";
import TeamMember2 from "../content/TeamMember2.json";

const Container = lazy(() => import("../common/Container"));
const ContentBlock = lazy(() => import("../components/ContentBlock"));

const About = () => {
  return (
    <Container>
      <ContentBlock
        type="right"
        title={TeamMember1.name}
        content={TeamMember1.description}
        icon={`${process.env.PUBLIC_URL}/img/svg/team1.svg`}
        id="team1"
      />
      <ContentBlock
        type="left"
        title={TeamMember2.name}
        content={TeamMember2.description}
        icon={`${process.env.PUBLIC_URL}/img/svg/team2.svg`}
        id="team2"
      />
    </Container>
  );
};

export default About;
